'use client';

import React from 'react';
import { useAuth } from '@/hooks/use-auth';
import { formatFullName } from '@/lib/formatters';
import { roleLabels } from '@/lib/roles/role-config';
import { Bell, Settings } from 'lucide-react';

export function Header() {
  const { user } = useAuth();
  
  if (!user) return null;

  return (
    <header className="h-16 w-full bg-white/10 backdrop-blur-md border-b border-white/20 flex items-center justify-between px-6 shadow-sm">
      
      {/* T√≠tulo y rol */}
      <div>
        <h2 className="text-lg md:text-xl font-bold text-white drop-shadow-md">
          Bienvenido, {user.nombre}
        </h2>
        <p className="text-sm text-gray-300">{roleLabels[user.rol]}</p>
      </div>
      
      {/* Acciones */}
      <div className="flex items-center gap-4">
        {/* Notificaciones */}
        <button className="relative p-2 rounded-xl hover:bg-white/20 transition-colors text-white">
          <Bell className="w-5 h-5" />
          <span className="absolute top-1 right-1 w-2 h-2 bg-red-500 rounded-full animate-pulse" />
        </button>

        {/* Configuraci√≥n */}
        <button className="p-2 rounded-xl hover:bg-white/20 transition-colors text-white">
          <Settings className="w-5 h-5" />
        </button>

        {/* Avatar y nombre */}
        <div className="flex items-center gap-3">
          <div className="w-10 h-10 bg-purple-600 text-white rounded-full flex items-center justify-center font-bold shadow-md">
            {user.nombre.charAt(0).toUpperCase()}
          </div>
          <div className="hidden md:flex flex-col">
            <p className="text-sm font-medium text-white drop-shadow-md">
              {formatFullName(user.nombre, user.apellido)}
            </p>
            <p className="text-xs text-gray-300 truncate max-w-[150px]">
              {user.email}
            </p>
          </div>
        </div>
      </div>
    </header>
  );
}
Header.displayName = 'Header';